<template>
  <div class="row" v-if="isReady">
    <div class="col-12 mt-3 flex-x-between">
      <div class="flex-align-center">
        <h3 class="my-0">Order Items</h3>
      </div>

      <div class="flex-align-center flex-end">
        <el-button
          class="mr-3 my-0"
          @click="printPdf"
          type="primary"
          v-ripple.400
        >
          Print
        </el-button>
      </div>
    </div>
    <div class="col-12 list-table mt-3">
      <div class="box pt-3">
        <div class="row">
          <div
            class="
              col-12
              mb-1
              d-flex
              justify-content-between
              align-items-center
            "
          >
            <div class="flex-align-center">
              <el-select
                v-model="filters.pageSize"
                class="mr-2 page-size-selector mr-2"
              >
                <el-option
                  v-for="page in 100"
                  :key="page"
                  :label="page * 100"
                  :value="page * 100"
                >
                </el-option>
              </el-select>
              <el-input
                v-model="filters.query"
                class="mr-2 table-search"
                placeholder="Type something"
                prefix-icon="bx bx-search"
              >
              </el-input>
            </div>

            <el-popover placement="bottom" width="200" trigger="click">
              <p class="font-weight-bold">Sort By</p>
              <div class="b-divider my-2"></div>
              <div class="w-100">
                <el-radio
                  v-model="filters.order_by"
                  v-for="(sort, n) in sortList"
                  :key="sort.name"
                  :class="n !== sortList.length - 1 && 'mb-3'"
                  :label="sort.value"
                >
                  {{ sort.name }}
                </el-radio>
              </div>
              <el-button
                v-ripple.400="'rgba(0,0,0,0.1)'"
                class="flat no-width"
                slot="reference"
              >
                <i class="bx bx-sort mr-1"></i> Sort
              </el-button>
            </el-popover>
          </div>
        </div>

        <div class="w-100 el-main-table" v-loading="loading">
          <el-table ref="multipleTable" :data="data" style="width: 100%">
            <el-table-column width="100" label="ID">
              <template slot-scope="scope">
                {{ scope.row.id }}
              </template>
            </el-table-column>
            <el-table-column label="Code" width="250">
              <template slot-scope="scope">
                <img
                  @click="openLightBox(scope.row.image)"
                  :src="scope.row.image"
                  style="
                    width: 50px;
                    height: 50px;
                    object-fit: cover;
                    border-radius: 5px;
                    margin-right: 5px;
                  "
                  alt=""
                />
                {{ scope.row.name }}
              </template>
            </el-table-column>

            <el-table-column label="Order ID">
              <template slot-scope="scope">
                {{ scope.row.sale }}
              </template>
            </el-table-column>
            <el-table-column label="Customer">
              <template slot-scope="scope">
                {{ scope.row.customer_name }}
              </template>
            </el-table-column>
            <el-table-column label="Delivery">
              <template slot-scope="scope">
                {{ scope.row.delivery }}
              </template>
            </el-table-column>
            <el-table-column label="Date">
              <template slot-scope="scope">
                {{ scope.row.created_at }}
              </template>
            </el-table-column>

            <template #empty>
              <el-empty description="No data found!">
                <template #image> <empty /> </template
              ></el-empty>
            </template>
          </el-table>
          <div class="el-table-footer" v-if="orders.total_pages !== 1">
            <el-pagination
              background
              @current-change="
                (index) => ((orders.current_page = index), getOrders())
              "
              :current-page="orders.current_page"
              layout="prev, pager, next"
              :page-count="orders.total_pages"
            >
            </el-pagination>
          </div>
        </div>
      </div>
    </div>

    <CoolLightBox
      :items="lightBoxFile"
      :useZoomBar="true"
      :index="index"
      @close="index = null"
    >
    </CoolLightBox>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { empty } from "../../common";
export default {
  data() {
    return {
      filters: {
        query: "",
        order_by: "-updated_at",
        pageSize: 10,
        supplier: "",
        date: "",
        is_fulfilled: false,
        delivery: "",
      },
      lightBoxFile: [],
      index: null,
      openFulfillDialog: false,
      openCreateMultipleDialog: false,
      editCodeDialog: false,
      creatingCode: false,
      creatingMultiple: false,
      updatingCode: false,
      loading: false,
      code: {
        code: "",
        app: "Pin Dou Dou",
        status: "Pending",
      },
      editingCode: {},
      actions: [
        {
          label: "Set as Received",
          value: "Received",
        },
        {
          label: "Set as Pending",
          value: "Pending",
        },
        {
          label: "Set as Cancelled",
          value: "Cancelled",
        },
        {
          label: "Delete",
          value: "Delete",
        },
      ],
      sortList: [
        {
          name: "Created (oldest first)",
          value: "created_at",
        },
        {
          name: "Created (newest first)",
          value: "-created_at",
        },
        {
          name: "Updated (oldest first)",
          value: "updated_at",
        },
        {
          name: "Updated (newest first)",
          value: "-updated_at",
        },
      ],
      suppliers: [],
      selectedDate: null,
      allCheck: false,
      gettingOrder: false,
      page: 1,
      max: 5,
      selected: [],
      data: [],
      orders: {
        current_page: 1,
        results: [],
        total_pages: 0,
      },
      cargos: [],
      loadingCargos: false,
      deletingCode: false,
      updatingMultiple: false,
      deliveries: [],
    };
  },
  computed: {
    ...mapState(["isReady", "store"]),
  },
  components: {
    empty,
  },
  created() {
    this.$store.commit("setIsReady", false);
    this.getOrders();
  },
  watch: {
    "orders.current_page": {
      handler: function () {
        this.getOrders();
      },
      deep: true,
    },
    filters: {
      handler: function () {
        this.getOrders();
      },
      deep: true,
    },
  },
  methods: {
    openLightBox(image) {
      this.lightBoxFile = [image];
      this.index = 0;
    },
    getOrders() {
      this.loading = true;
      this.gettingOrder = true;
      if (this.filters.date === null) {
        this.filters.date = "";
      }
      var url = `sale-products-os/?page=${this.orders.current_page}&page_size=${this.filters.pageSize}&query=${this.filters.query}&order_by=${this.filters.order_by}`;
      this.$axios
        .get(url)
        .then((response) => {
          this.loading = false;
          this.gettingOrder = false;
          this.data = response.data.results;
          this.orders.total_pages = response.data.total_pages;
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.loading = false;
          this.gettingOrder = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
  },
};
</script>

